import { HTMLAttributes, useState } from "react";

/**
 *    .
 *
 * @param {string} classname -    .
 * @param {string} placeholder -    .
 * @param {string} name -   .
 * @param {string} type -    (, "text"  "password").
 * @param {boolean} required -    .
 * @param {(string | undefined | number)} value -   .
 * @param {Function} onChangeHandler -      .
 * @param {string} autoComplete -    .
 * @param {string} error -  ( ).
 * @returns {JSX.Element} - JSX     .
 */

interface CustomInputProps extends HTMLAttributes<HTMLInputElement> {
  classname?: string;
  placeholder: string;
  name: string;
  type: string;
  required?: boolean;
  value: string | undefined | number;
  onChangeHandler: any;
  autoComplete: string;
  error?: string;
}

export const CustomInput: React.FC<CustomInputProps> = ({
  classname,
  placeholder,
  name,
  type,
  required,
  value,
  onChangeHandler,
  autoComplete,
  error,
  ...props
}): JSX.Element => {
  const [placeholderColor, setPlaceholderColor] = useState("");
  const [borderOnFocus, setBorderOnFocus] = useState("");
  const currentColor = error ? "border-red-400" : "";

  const handleOnFocus = () => {
    setPlaceholderColor("placeholder:text-violetStroke");
    setBorderOnFocus("border-violetStroke");
  };

  const handleOnBlur = () => {
    setPlaceholderColor("");
    setBorderOnFocus("");
  };

  return (
    <input
      placeholder={placeholder}
      name={name}
      type={type}
      className={`border-b ${currentColor} ${placeholderColor} ${borderOnFocus} placeholder:font-medium focus:outline-none px-4 py-2 w-full ${classname} font-['Roboto_flex']`}
      required={required}
      value={value}
      onChange={onChangeHandler}
      autoComplete={autoComplete}
      onFocus={handleOnFocus}
      onBlur={handleOnBlur}
      {...props}
    />
  );
};
